.container8 {
   position: relative;
   overflow: hidden;
   border-radius: 0rem;
   /* background-color: #f5f5f5; */
}
/* @media only screen and (max-width: 1000px) {
   .container {
      border-radius: 0;
   }
} */
.container8 .section-heading {
   margin-bottom: 0;
   padding-bottom: 0;
}
.slider {
   /* background-color: #f5f5f5; */
   display: -webkit-box;
   display: flex;
   width: 500%;
   height: 100vh;
   -webkit-transition: all 0.25s ease-in;
   transition: all 0.25s ease-in;
   -webkit-transform: translateX(0);
   transform: translateX(0);
}

/* @media only screen and (max-width: 1000px) {
   .slider {
      height: 100vh;
   }
} */
.slider .box {
   height: 100%;
   width: 100%;
   display: grid;
   grid-template-columns: repeat(2, 1fr);
   -webkit-box-align: center;
   align-items: center;
   overflow: hidden;
   position: relative;
}
@media only screen and (max-width: 650px) {
   .slider .box {
      grid-template-columns: 1fr;
      grid-template-rows: repeat(2, 1fr);
   }
}
.slider .box .bg {
   padding: 2rem;
   background-color: rgba(0, 0, 0, 0.068);
   z-index: 7;
   width: 55%;

   -webkit-transform: skewX(7deg);
   transform: skewX(7deg);
   position: absolute;
   height: 100%;
   left: -10%;
   opacity: 100%;
   padding-left: 20rem;
   -webkit-transform-origin: 0 100%;
   transform-origin: 0 100%;
}
@media only screen and (max-width: 800px) {
   .slider .box .bg {
      width: 65%;
   }
}
@media only screen and (max-width: 650px) {
   .slider .box .bg {
      width: 100%;
      left: 0;
      bottom: 0;
      height: 58%;
      -webkit-transform: skewX(0deg);
      transform: skewX(0deg);
   }
}
.slider .box .bg::before {
   content: '';
   width: 100%;
   height: 100%;
   position: absolute;
   left: 0;
   top: 0;

   background-color: inherit;
   pointer-events: none;
   -webkit-transform: skewX(10deg);
   transform: skewX(10deg);
}
.bg {
   display: none;
}
@media only screen and (max-width: 650px) {
   .slider .box .bg::before {
      width: 120%;
      bottom: 0;
      -webkit-transform: skewX(0deg);
      transform: skewX(0deg);
   }
}
.slider .box .details {
   padding: 0rem;
   padding-left: 10rem;
   z-index: 100;
   grid-column: 1 / span 1;
   grid-row: 1 / -1;
}
@media only screen and (max-width: 650px) {
   .slider .box .details {
      grid-row: 2 / span 1;
      grid-column: 1 / -1;
      text-align: center;
      padding: 2rem;
      -webkit-transform: translateY(-9rem);
      transform: translateY(-9rem);
   }
}
.slider .box .details .ha {
   font-size: 3rem;
   font-weight: 600;
   padding-bottom: 2rem;
   margin-top: 0;
   margin-bottom: 2rem;
   opacity: 1 !important;
}
.slider .box .details .pa {
   display: inline-block;
   font-size: 1.3rem;
   color: gray;
   opacity: 1 !important;
   margin-bottom: 3rem;
   margin-right: 5rem;
}
@media only screen and (max-width: 800px) {
   .slider .box .details .pa {
      margin-right: 0;
   }
}
.slider .box .details .button {
   padding: 1rem 3rem;

   position: relative;
   top: 0;
   left: 0;
   border-radius: 0rem;
   outline: none;
   border: none;
   cursor: pointer;
   /* -webkit-transition: all 0.3s ease;
   transition: all 0.3s ease; */
}
.slider .box .details .button :hover {
   opacity: 0.8;
}
.slider .box .details .button :focus {
   outline: none;
   border: none;
}

.slider .illustration {
   grid-column: 2 / -1;
   grid-row: 1 / -1;
   justify-self: center;
}
@media only screen and (max-width: 650px) {
   .slider .illustration {
      grid-row: 1 / span 1;
      grid-column: 1 / -1;
      display: -webkit-box;
      display: flex;
      -webkit-box-pack: center;
      justify-content: center;
      -webkit-box-align: center;
      align-items: center;
   }
   .dummy {
      padding: 5%;
      margin-left: 10%;
   }
}
.slider .illustration .a {
   height: 25rem;
   width: 18rem;
   border-radius: 0rem;

   position: relative;
   -webkit-transform: skewX(-10deg);
   transform: skewX(-10deg);
}
@media only screen and (max-width: 800px) {
   .slider .illustration .a {
      height: 20rem;
      width: 15rem;
   }
}
.slider .illustration .a::after,
.slider .illustration .a::before {
   content: '';
   position: absolute;
   height: 100%;
   width: 100%;
   border-radius: 3rem;
   top: 0;
   left: 0;
}
.slider .illustration .a::after {
   -webkit-transform: translate(4rem, -1rem);
   transform: translate(4rem, -1rem);
}
.slider .illustration .a::before {
   -webkit-transform: translate(2rem, -2rem);
   transform: translate(2rem, -2rem);
}

.prev,
.next,
.trail {
   z-index: 100;
   position: absolute;
}

.prev,
.next {
   width: 7rem;
   cursor: pointer;
   opacity: 1;
   /* transform: scale(1.5); */
   -webkit-transition: all 0.3s ease;
   transition: all 0.3s ease;
}
@media only screen and (max-width: 650px) {
   .prev,
   .next {
      display: none;
   }
}
.prev:hover,
.next:hover {
   opacity: 0.8;
}

.prev {
   top: 60%;
   left: 2%;
   -webkit-transform: translateY(-50%);
   transform: translateY(-50%);
}

.next {
   position: absolute;
   top: 60%;
   left: 92%;
   -webkit-transform: translateY(-50%);
   transform: translateY(-50%);
}

.trail {
   bottom: 0%;
   left: 50%;
   -webkit-transform: translateX(-50%);
   transform: translateX(-50%);
   width: 60%;
   display: grid;
   grid-template-columns: repeat(5, 1fr);
   gap: 1rem;
   text-align: center;
   font-size: 1.5rem;
}
@media only screen and (max-width: 650px) {
   .trail {
      width: 90%;
      bottom: 3%;
   }
}
.trail .a {
   padding: 2rem;
   border-top: 3px solid black;
   cursor: pointer;
   opacity: 0.3;
   -webkit-transition: all 0.3s ease;
   transition: all 0.3s ease;
}
.trail .a:hover {
   opacity: 0.6;
}
@media only screen and (max-width: 650px) {
   .trail .a {
      padding: 1rem;
   }
}

.active {
   opacity: 1 !important;
}
.dummy {
   width: 80%;
   position: relative;
   right: 0%;
}
@media only screen and (max-width: 600px) {
   .bg {
      display: none;
   }
   .dummy {
      width: 120%;
      position: relative;
      right: 7rem;
      bottom: 3.4rem;
      z-index: 9999;
   }
   .slider .box .details .pa {
      font-size: 1.8rem;
      font-weight: 600;
   }
   .details {
      margin-top: 1rem;
   }
   .container8 {
      padding-top: 10%;
      /* background-color: #f5f5f5; */
      overflow-y: auto !important;
   }
   .slider {
      /* background-color: #f5f5f5; */
      height: 60rem !important;
   }
   .section-heading {
      padding-bottom: 4rem !important;
   }
   .container8 .section-heading {
      margin-bottom: 4rem;
   }
   .ha {
      font-size: 2.6rem;
      margin-top: 10px !important;
   }
}
/* @media only screen and (max-width: 900px) {
} */
@media only screen and (min-device-width: 700px) and (max-device-width: 1024px) and (orientation: portrait) {
   .slider {
      height: 60vh;
   }
   .ha {
      width: 100%;
      text-align: center;
      position: absolute;
      top: 5%;
      left: 0%;
   }
   .prev {
      left: 0;
      top: 55%;
   }
   .next {
      top: 55%;
   }
}
