.button {
   padding: 1.2rem 2.4rem;
   background: none;
   cursor: none;
   border: none;
   font-family: 'IBM Plex Sans', sans-serif;
   color: black;
   position: relative;
   /* text-transform: uppercase; */
   font-size: 1rem;
   font-weight: bold;
   letter-spacing: 0.3rem;
   cursor: pointer;
}
.button:after,
.button:before {
   content: '';
   position: absolute;
   top: 0;
   left: 0;
   bottom: 0;
   right: 0;
   border: 2px solid black;
   transition: transform 0.2s;
}
.button:hover {
   transition: 0.4s;
   background-color: #000;
   color: #fff;
}
