.backww::before {
   z-index: 0;
   -webkit-animation: grainss 10s steps(10) infinite;
           animation: grainss 10s steps(10) infinite;
   background-image: url(https://tympanus.net/Development/CSSMarqueeMenu/img/noise.png);
   content: '';
   height: 300%;
   left: -50%;
   opacity: 0.6;
   position: fixed;
   top: -100%;
   width: 300%;
}
a,
button,
input {
   z-index: 11;
}

@-webkit-keyframes grainss {
   0%,
   100% {
      transform: translate(0, 0);
   }

   10% {
      transform: translate(-5%, -10%);
   }

   20% {
      transform: translate(-15%, 5%);
   }

   30% {
      transform: translate(7%, -25%);
   }

   40% {
      transform: translate(-5%, 25%);
   }

   50% {
      transform: translate(-15%, 10%);
   }

   60% {
      transform: translate(15%, 0%);
   }

   70% {
      transform: translate(0%, 15%);
   }

   80% {
      transform: translate(3%, 35%);
   }

   90% {
      transform: translate(-10%, 10%);
   }
}

@keyframes grainss {
   0%,
   100% {
      transform: translate(0, 0);
   }

   10% {
      transform: translate(-5%, -10%);
   }

   20% {
      transform: translate(-15%, 5%);
   }

   30% {
      transform: translate(7%, -25%);
   }

   40% {
      transform: translate(-5%, 25%);
   }

   50% {
      transform: translate(-15%, 10%);
   }

   60% {
      transform: translate(15%, 0%);
   }

   70% {
      transform: translate(0%, 15%);
   }

   80% {
      transform: translate(3%, 35%);
   }

   90% {
      transform: translate(-10%, 10%);
   }
}

