.over-lay {
   position: absolute;
   right: 0;
   top: -120vh;
   padding: 3em;
   overflow: hidden;
   width: 100%;
   height: 100vh;
   transform-origin: top;
   opacity: 0.5;
   z-index: 1000;
   background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
   overflow: hidden;
   transition: all 0.6s ease-in-out;
}
.over-lay .cross {
   float: right;
   cursor: pointer;
}
.over-lay ul {
   padding-left: 0 !important;
   display: flex;
   justify-content: space-evenly;
   margin-top: 38vh;
}
.over-lay ul li {
   text-align: center;
   list-style: none;
   opacity: 0;
   transition: all 0.5s;
   font-weight: 700;
}
.over-lay ul li a {
   font-size: 3rem;
   color: white;
   transition: all 0.3s;
}
.over-lay ul li a:hover {
   color: whitesmoke;
}
.over-lay ul li span {
   display: block;
   margin: 1px 0;
   font-weight: 300;
   color: white;
   font-size: 1.2em;
}

.show {
   transition: all 0.6s ease-in-out;
   display: block;
   opacity: 1;
   visibility: visible;
   top: 0;
}
.show li:nth-of-type(1) {
   -webkit-animation: show-list 0.8s forwards 1s ease-out;
           animation: show-list 0.8s forwards 1s ease-out;
}
.show li:nth-of-type(2) {
   -webkit-animation: show-list 0.8s forwards 1.2s ease-out;
           animation: show-list 0.8s forwards 1.2s ease-out;
}
.show li:nth-of-type(3) {
   -webkit-animation: show-list 0.8s forwards 1.4s ease-out;
           animation: show-list 0.8s forwards 1.4s ease-out;
}
.show li:nth-of-type(4) {
   -webkit-animation: show-list 0.8s forwards 1.6s ease-out;
           animation: show-list 0.8s forwards 1.6s ease-out;
}

@-webkit-keyframes show-list {
   0% {
      opacity: 0;
      transform: translateY(200px);
   }
   100% {
      transform: translateY(0);
      opacity: 1;
   }
}

@keyframes show-list {
   0% {
      opacity: 0;
      transform: translateY(200px);
   }
   100% {
      transform: translateY(0);
      opacity: 1;
   }
}
